/* 1024 i 1280 */

@media (min-width:1020px) and (max-width:1380px) {

    .mobile {
        max-width: 130%;
        margin-left: -6vw;
        height: auto;
    }

    /* div.container.mt-5 {
        transform: scale(0.9)
    } */
}

/* 800 */

@media (min-width:780px) and (max-width:1019px) {

    .mobile {
        max-width: 130%;
        margin-left: -6vw;
        height: auto;
    }

    .left {
        margin-left: 33%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

/* 768 */
@media (min-width:770px) and (max-width:779px) {

    .mobile {
        max-width: 130%;
        margin-left: -6vw;
        height: auto;
    }

    div.row.justify-content-md-center {
        margin-left: 30px;
    }

    .left {
        margin-left: 33%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

